.cart {
    width: 100%;
    text-align: center;
  }
  h1{
    padding: 20px;
    font-size: 30px;
    font-weight: 700;
  }
  
  .myCart {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    
  }
  .cartProd {
    padding: 10px 20px;
    width: 70%;
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 15px;
  }
  .cartList {
    width: 25%;
    height: auto;
    text-align: center;
    background-color: #000000;
    color: #fff;
    margin: 10px 20px;
  }
  
  .myList {
    width: 100%;
  }
  .single {
    padding: 10px 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .listTotal {
    border-top: 1px solid #fff;
    border-bottom: 1px solid #fff;
    padding: 10px 20px;
    width: 100%;
    display: flex;
    justify-content: space-between;
  }
  
  .checkOut {
    background-color: #fff;
    color: #000;
    outline: none;
    border: none;
    margin: 20px;
    font-size: 16px;
    font-weight: 700;
    padding: 15px;
  }
  
  .cartList h3 {
    padding: 20px 0;
    font-size: 20px;
  }